@import "slick-carousel/slick/slick.css";
@import "slick-carousel/slick/slick-theme.css";
@import "../../styles/variables.scss";

.carousel-container {
    text-align: center;
    padding: 2rem 0;
    width: 100%;
    align-items: center;
    justify-content: center;
  
    h2 {
      color: #7A232E;
      margin-bottom: 1rem;
    }
  
    .slick-slider {
      .slick-prev,
      .slick-next {
        background-color: black;
        border-radius: 50%;
      }
    }
  
    .carousel-item {
      display: flex !important;
      flex-direction: column;
      align-items: center;
      text-align: center;
      justify-content: center;
      width: 100%;
  
      .category-image {
        width: 10vw;
        height: 10vw;
        border-radius: 50%;
        overflow: hidden;
        margin-bottom: 10px;
        background-color: white;
        border: 2px solid $siman-primary;
        align-items: center;
        text-align: center;
        display: flex;
        justify-content: center;
 
        
        img {
          width: 80%;
          max-width: 100%;
          max-height: 100%;
          top: 50%;
          object-fit: cover;
        }
      }
  
      p {
        color: $siman-secondary;
        font-size: 1rem;
      }
    }
  }
  
  .text-carousel {
    text-align: center;
    color: $siman-primary;
    font-size: 25px;
    font-family: "Abhaya Libre", serif;
    font-weight: 500;
    line-height: 28.8;
    letter-spacing: 0.15;
    word-wrap: break-word;
  }

  .product-card {
    background: white;
    border: 1px solid #ddd;
    border-radius: 20px;
    overflow: hidden;
    width: 15vw;
    height: 30rem;
    text-align: left;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
  
    .product-image {
      padding-top: 1rem;
      width: 100%;
      height: 250px;
      object-fit: contain;
    }
  
    .product-info {
      padding: 10px;
      display: block;
  
      .product-title {
        font-size: 14px;
        margin: 5px 0;
      }
  
      .product-price {
        font-size: 16px;
        font-weight: bold;
        color: black;
        bottom: 10%;
      }

      .product-rate {
        font-size: 12px;
        color: white;
        width: 30%;
        align-items: center;
        text-align: center;
        display: flex;
        justify-content: right;
        padding: 5px 10px;
        border-radius: 40px;
        margin: 5px 0;
        background-color: black;
      }

      .product-seller {
        font-size: 10px;
        color: black;
        margin: 5px 0;
      }

    }
    &:hover {
      .product-button {
        display: block;
      }
    }
  }

  .product-button {
    background-color: $siman-primary;
    color: white;
    border: none;
    border-radius: 5px;
    padding: 5px 10px;
    font-size: 14px;
    cursor: pointer;
    margin-top: 10px;
    display: none;
    width: 100%;
    height: 2.5rem;
    padding-bottom: -1.5rem;

  }

  .star {
    color: #F38723;
    font-size: 10px;
  }